// вспомогателный модуль
@desktop-width: 1200px;
@mobile-width-only: 767px;

body {
    margin: 0;
    padding: 0;

    font-family: 'Gothic A1', sans-serif;

    .visually-hidden:not(:focus):not(:active),
    input[type="checkbox"].visually-hidden,
    input[type="radio"].visually-hidden {
        position: absolute;

        width: 1px;
        height: 1px;
        margin: -1px;
        padding: 0;
        overflow: hidden;

        white-space: nowrap;

        border: 0;

        clip: rect(0 0 0 0);
        clip-path: inset(100%);
    }
}
